export default {
	data() {
		return {
			phone: uni.getStorageSync('phone'),
			Cashlist: []
		}
	},
	onLoad() {
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/t_cash_list',
			method: "POST",
			data: {
				phone: this.phone
			},
			success: res => {
				console.log(res)
				this.Cashlist = res.data.reverse()
			}
		})
	}
}
